export const promiseWithTimeout = promise => {
    let timeoutId
    const timeoutPromise = new Promise((_, reject) => {
        timeoutId = setTimeout(() => reject(new Error('Promise timed out')), 3600 * 5 * 1000)
    })

    return {
        promiseOrTimeout: Promise.race([promise, !window.document.hidden ? timeoutPromise : null]),
        timeoutId
    }
}


export const sleep = (time = 10000) => {
    let timer;
    return new Promise(resolve => {
        clearTimeout(timer)
        timer = setTimeout(resolve, time)
    })
}